/* Basic resets and font settings */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.app {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  background: linear-gradient(to right, rgb(0, 218, 0), rgb(0, 0, 100));
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.menu-toggle {
  background: transparent;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.logout-button {
  background: transparent;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  margin-left: auto;
}

.sidebar {
  width: 200px;
  background: linear-gradient(to bottom, rgb(0, 218, 0), rgb(0, 0, 100));
  padding: 20px;
  position: fixed;
  top: 40px;
  bottom: 0;
  left: 0;
  overflow-y: auto;
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  z-index: 2;
}

.sidebar.hidden {
  transform: translateX(-100%);
}

.content {
  position: relative;
  margin-left: 220px;
  padding: 20px;
  top: 40px;
  width: calc(100% - 220px);
  max-width: calc(100% - 270px);
  overflow: auto;
  transition: margin-left 0.3s ease;
}

.sidebar.hidden + .content {
  margin-left: 0px;
  width: 100%;
}

/* Sidebar button styling */
.sidebar button {
  background: transparent;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  padding: 10px;
  margin: 10px 0;
  text-align: left;
  width: 100%;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Active button highlight */
.sidebar button.active {
  background-color: rgba(255, 255, 255, 0.2);
  color: yellow;
}

/* Responsive styles */
@media (max-width: 768px) {
  .sidebar {
    width: 200px;
    top: 40px;
    display: flex; /* Hide sidebar on small screens by default */
  }
  

  .content {
    margin-left: 0;
    width: 100%;
    max-width: none;
  }
}

@media (min-width: 769px) {
  .menu-toggle {
    display: block; /* Show toggle button on large screens */
  }
}
.sidebar .visible{
  display: block;
}
div{
  text-align: center;
}
table th{
  text-align: left;
}
table td{
  text-align: left;
}